import React, { Component } from 'react';
import './App.css';
import YouTube from 'react-youtube';
import { SocketProvider } from 'socket.io-react';
import io from 'socket.io-client';
import CreatePublicChannelPanel from './Components/CreatePublicChannelPanel/CreatePublicChannelPanel';
import CreatePrivateChannelPanel from './Components/CreatePrivateChannelPanel/CreatePrivateChannelPanel';
import JoinPublicChannelPanel from './Components/JoinPublicChannelPanel/JoinPublicChannelPanel';
import JoinPrivateChannelPanel from './Components/JoinPrivateChannelPanel/JoinPrivateChannelPanel';

class App extends Component {

  state = {
    createChannel:false,
    joinChannel: false,
    publicChannel:false,
    privateChannel:false,
    showCreatePublicChannelPanel:false,
    showCreatePrivateChannelPanel:false,
    showJoinPublicChannelPanel:false,
    showJoinPrivateChannelPanel:false

  }

  handleCreateChannel = () => {
    this.setState({
      createChannel:true,
      joinChannel:false,
      showJoinPublicChannelPanel:false,
      showJoinPrivateChannelPanel:false
    })
  }

  handleJoinChannel = () => {
    this.setState({
      createChannel:false,
      joinChannel:true,
      showCreatePublicChannelPanel:false,
      showCreatePrivateChannelPanel:false
    })
  }

  handleCreatePublicChannel = () => {
    this.setState({
      publicChannel:true,
      showCreatePublicChannelPanel:true,
      showCreatePrivateChannelPanel:false
    })
  }

  handleCreatePrivateChannel = () => {
    this.setState({
      privateChannel:true,
      showCreatePrivateChannelPanel:true,
      showCreatePublicChannelPanel:false
    })
  }

  handleJoinPublicChannel = () => {
    this.setState({
      showJoinPublicChannelPanel:true,
      showJoinPrivateChannelPanel:false
    })
  }


  handleJoinPrivateChannel = () => {
    this.setState({
      showJoinPublicChannelPanel:false,
      showJoinPrivateChannelPanel:true
    })
  }

  playBackHandler = (event, action) => {
      // access to player in all event handlers via event.target
      const socket = io.connect('https://sync-in.herokuapp.com/');
      socket.on('play', () => {
        event.target.playVideo();
      });
      socket.on('pause', () => {
        event.target.pauseVideo();
      });

      if(action === 'play'){
        socket.emit('play');
      }
      if(action === 'pause'){
        socket.emit('pause');
      }
  }

  render() {
    console.log("hhhhh");
    const opts = {
      height: '390',
      width: '640'
    };
    return (
      <SocketProvider>
        <div className="App">
          <h1>Welcome to Sync In</h1>
          <p>A web app that allows you to synchronize Youtube playback so that you can listen to music and watch videos together with your friends.</p>
          <p>The principle is simple - if you play or pause the video, it will do the same to the person you're using Sync In with.</p>
          <div id="create-vs-join-panel">
            <h3>Do you want to create or join already existing channel?</h3>
            <div className="select-div" onClick={this.handleCreateChannel}>Create Channel</div>
            <div className="select-div" onClick={this.handleJoinChannel}>Join Channel</div>
          </div>
    
          <div id="public-vs-private-panel">
            
            {this.state.createChannel? (
              <div>
                <h3>Do you want to create a public or private chennel?</h3>
                <div className="select-div" onClick={this.handleCreatePublicChannel}>create public channel</div>
                <div className="select-div" onClick={this.handleCreatePrivateChannel}>create private channel</div>
              </div>
            ):(null)}

            {this.state.joinChannel? (
              <div>
                <h3>Do you want to join a public or private chennel?</h3>
                <div className="select-div" onClick={this.handleJoinPublicChannel}>join public channel</div>
                <div className="select-div" onClick={this.handleJoinPrivateChannel}>join private channel</div>
              </div>
            ):(null)}

          </div>
            { this.state.showCreatePublicChannelPanel? (<CreatePublicChannelPanel/>):(null)}
            { this.state.showCreatePrivateChannelPanel? (<CreatePrivateChannelPanel/>) :(null)}
            { this.state.showJoinPublicChannelPanel? (<JoinPublicChannelPanel/>) : (null) }
            { this.state.showJoinPrivateChannelPanel? (<JoinPrivateChannelPanel/>) : (null) }
            <YouTube
              id="youtube-player"
              videoId="-VsmF9m_Nt8"
              opts={opts}
              onPlay={(e) => {this.playBackHandler(e, 'play')}}
              onPause={(e) => {this.playBackHandler(e, 'pause')}}
              onReady={this.playBackHandler}
            />

        </div>
      </SocketProvider>
    );
  }
}


export default App;
